@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  padding: 0;
}

.main {
  display: flex;
  flex-direction: column;
}

.head {
}

.body {
  display: flex;
  flex-direction: row;
}

.navbar {
  display: flex;
  flex-direction: column;
  width: 17%;
  background-color: slategrey;
}

.navbar__title {
  display: flex;
  height: 11vh;
  justify-content: center;
  align-items: center;
  background-color: slategrey;
  font-size: 24px;
  font-weight: 800;
}

.months {
  display: flex;
  width: 100%;
  flex-direction: column;
}

.month {
  width: 100%;

  height: 90px;
  display: flex;

  background-color: royalblue;
  margin-bottom: 10px;
  justify-content: right;
}

.month__wrapper {
  width: 87%;
  background-color: rgb(231, 231, 231);
  justify-content: center;

  height: 100%;
  display: flex;
  align-items: center;
  flex-direction: column;

  font-size: 20px;
}

.positive {
  background-color: yellowgreen;
}

.negative {
  background-color: orangered;
}

.month__title {
}

.month__amount {
}

.content {
  width: 55%;
  height: 88vh;
  /* background-color: aqua; */
}

.content__title {
  background-color: royalblue;
  display: flex;
  height: 11vh;
  justify-content: center;
  align-items: center;

  font-size: 24px;
  font-weight: 800;
  /* box-shadow: 5px 5px 10px; */
}

.content__summary {
  /* background-color: yellow; */
  width: 100%;

  display: flex;
  height: 77vh;
  align-items: center;
  justify-content: center;
}

.summary {
  background-color: rgb(206, 206, 206);
  width: 90%;
  height: 90%;
  display: flex;
  flex-direction: column;
  align-items: center;
  border-radius: 10px;
}

.summary__title {
  width: 100%;
  /* background-color: wheat; */
  display: flex;
  align-items: center;
  justify-content: center;
  height: 7vh;
  font-size: 20px;
  font-weight: 600;
}

.summary__resumme {
  display: flex;
  flex-direction: row;
  height: 10%;
  width: 100%;
  background-color: purple;
}

.summary__resume-row {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 100%;
  justify-content: space-around;
  /* margin: 5px; */
}

.summary__resume-item {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  background-color: rgb(231, 231, 231);
  font-size: 20px;
  font-weight: 600;
}

.summary__resumme--title {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
  width: 100%;
  height: 7%;
  background-color: royalblue;
}

.summary__content-income {
  background-color: blueviolet;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.summary__content-outcome {
  background-color: royalblue;
  width: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.summary__content {
  height: 100%;
  width: 100%;
}

/* Movements */

.summary__movements {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  font-weight: 600;
  width: 100%;
  height: 85%;

  /* height: 10%; */
  /* background-color: aquamarine; */
}

.summary__movements-title {
  height: 10%;
  width: 100%;
  background-color: royalblue;
  display: flex;
  justify-content: center;
  align-items: center;
}

.movements {
  height: 100%;
  width: 100%;
  background-color: slategrey;
  font-size: 20px;
  border-radius: 0px 0px 10px 10px;

  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  /* align-items: center; */
  padding: 10px;
}

.movements__legend {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.movement {
  background-color: rgb(231, 231, 231);
  display: grid;
  grid-gap: 5px;
  gap: 5px;
  grid-template-columns: repeat(4, 25%);
  justify-content: start;
  justify-items: start;
  box-shadow: 0px 15px 10px -15px #111;
  margin-bottom: 1px;
  border-radius: 10px;
}

.movement__item {
  display: flex;
  /* background-color: aqua; */
  align-items: center;
  justify-content: center;
  padding: 5px 0px 5px 20px;
}

.header {
  display: flex;
  flex-direction: row;
  text-align: center;
  /* justify-content: center; */

  width: 100%;
  height: 8vh;
  background-color: #d2d2d2;
}

.header__welcome {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 83%;
  height: 100%;
  background-color: #b4b4b4;
  /* background-color: #dd2; */
}

.header__welcome-icon {
  font-size: 45px;
  margin-left: 20px;
  padding: 5px;
  /* justify-self: start; */
}

.header__welcome-title {
  font-size: 30px;
  margin-left: 40%;
  padding: 5px;
}

.header__config {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  width: 17%;
  height: 100%;
  padding-right: 10px;

  /* background-color: #dd2; */
}

.header__config-icon {
  font-size: 40px;
  cursor: pointer;
  /* background-color: antiquewhite; */
}

.form {
  display: flex;
  flex-direction: column;
  /* justify-content: center; */
  align-items: center;
  width: 28%;
  background-color: aqua;
}

.form:last-child {
  bottom: 0;
}

.form-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 15px;
  margin-left: 30px;
}

.form-button {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin-top: 15px;
  margin-left: 30px;
  /* cursor: pointer; */
}

.input-button {
  cursor: pointer;
  padding: 15px;
  text-decoration: none;
  border: none;
  border-radius: 10px;
  font-size: 16px;
  font-weight: 600;
  background-color: greenyellow;
}

.input-button:hover {
  background-color: green;
  color: white;
}

.form-radio {
}

.form-text {
  display: flex;
  width: 70%;
  height: 30px;
  border: none;
  /* border-bottom: 1px solid black; */
  font-size: 16px;
  border-radius: 10px;
  font-weight: 600;
  text-decoration: none;
  padding-left: 10px;
  justify-content: center;
  align-items: center;
}

.form-text:focus {
  outline: none;
}

.form-column {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  margin-top: 15px;
  margin-left: 30px;
}

.form-title {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 11vh;
  width: 100%;
  background-color: royalblue;
  font-size: 24px;
  font-weight: 800;
}

